"use client"
import { useEffect, useState } from 'react'
import { useContract } from '@/context/contract-context'
import { X } from 'lucide-react'
import { HeroSection } from '@/components/sections/HeroSection'
import { HowItWorksSection } from '@/components/sections/how-it-works'

// Wallet Connection Dialog Component
const WalletConnectionDialog = ({ onClose }) => {
  const { connectWallet } = useContract()

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black bg-opacity-50">
      <div className="bg-white dark:bg-gray-800 p-6 rounded-lg shadow-lg max-w-md w-full">
        <div className="flex justify-between items-center mb-4">
          <h3 className="text-lg font-semibold text-gray-900 dark:text-gray-100">Connect Your Wallet</h3>
          <button onClick={onClose} className="text-gray-500 hover:text-gray-700">
            <X size={20} />
          </button>
        </div>
        
        <div className="mb-6">
          <p className="text-gray-700 dark:text-gray-300 mb-4">
            This application works best when connected to your wallet. Connect to access all features and participate in campaigns.
          </p>
          
          <div className="bg-blue-50 dark:bg-blue-900/20 border-l-4 border-blue-500 p-4 mb-4">
            <p className="font-medium text-blue-700 dark:text-blue-300 mb-1">Safe to use:</p>
            <ul className="list-disc pl-5 text-gray-600 dark:text-gray-400">
              <li>No funds will be used unless you explicitly sign a transaction</li>
              <li>You'll always be asked for confirmation before signing</li>
              <li>You can disconnect your wallet at any time</li>
            </ul>
          </div>
        </div>
        
        <div className="flex justify-center">
          <button 
            onClick={connectWallet}
            className="px-6 py-3 bg-blue-600 hover:bg-blue-700 text-white font-medium rounded-lg transition-colors"
          >
            Connect Wallet
          </button>
        </div>
        
        <p className="text-xs text-center text-gray-500 mt-4">
          By connecting, you're only giving permission to view your address. Full permission is only requested when you interact with contracts.
        </p>
      </div>
    </div>
  )
}

// Updated Page Component
const Page = () => {
  const { contract, address, chainId, isConnected, provider } = useContract();
  const [showDialog, setShowDialog] = useState(false)
  
  const listenForEvents = async () => {
    if (contract) {
      contract.on("CampaignCreated", (campaignId, creator, votingType, restriction, startTime, endTime, event) => {
        console.log("New Campaign Created, listened:", {
          campaignId: campaignId.toString(),
          creator,
          votingType,
          restriction,
          startTime: startTime.toString(),
          endTime: endTime.toString(),
          transactionHash: event.transactionHash
        });
      });
    }
  };
  
  useEffect(() => {
    if (contract) {
      listenForEvents();
    }
  }, [contract]);
  
  // Show dialog when user is not connected
  useEffect(() => {
    if (!isConnected) {
      // Small delay to avoid flash on initial load
      const timer = setTimeout(() => {
        setShowDialog(true)
      }, 1000)
      
      return () => clearTimeout(timer)
    } else {
      setShowDialog(false)
    }
  }, [isConnected]);
  
  return (
    <div>
      <HeroSection />
      <HowItWorksSection />
      <ActiveCampaigns />
      <FeaturesSection />
      <CTASection />
      
      {showDialog && <WalletConnectionDialog onClose={() => setShowDialog(false)} />}
    </div>
  )
}

export default Page